<template>
  <div class="modal-body">
    <Form
      @submit="sendEmail" 
      id="contact" 
      class="input-box"
      :validation-schema="schema"
    >
      <TextInput
        name="name"
        type="text"
        label=" "
        placeholder="   ."
      />
      <TextInput
        name="email"
        type="email"
        label=""
        placeholder=" ."
      />
      <TextInput
        name="tel"
        type="tel"
        label=""
        placeholder="'-'   ."
      />
      <div class="TextInput">
        <label for="message"></label>
        <textarea 
          name="message" 
          id="message"
          placeholder=" " 
        >
        </textarea>
      </div>
    </Form>
  </div>
  <div class="modal-footer">
    <input id="submit-btn" type="submit" form="contact" value="" class="btn btn--primary col-12 btn-lg">
  </div>
</template>

<script>
  import emailjs from '@emailjs/browser';

  import { Form } from "vee-validate";
  import * as Yup from "yup";
  import TextInput from "./AppTextInput";

  export default {
    name: "AppContact",
    data() {
      return {
        success: true,
      }
    },
    components: {
      TextInput,
      Form,
    },
    setup() {
      function sendEmail() {
        emailjs.sendForm(
          'service_2esgxbd', 
          'template_rb8n6yb', 
          document.querySelector('#contact'), 
          'lCBUF2uvwt97SJOoh'
        )
        .then((result) => {
          alert(' .', result.text);
        }, (error) => {
          alert('  ...', error.text);
        });
      }

      // Using yup to generate a validation schema
      // https://vee-validate.logaretm.com/v4/guide/validation#validation-schemas-with-yup
      const schema = Yup.object().shape({
        name: Yup.string().min(2, '2  ').required(' '),
        email: Yup.string().email('  ').required(' '),
        tel: Yup.number().typeError(' ').min(11, '11  ').required(' '),
      });

      return {
        sendEmail,
        schema,
      };
    },
  };
  </script>

<style lang="scss" scoped>
</style>