<script>
  import { spline } from '@georgedoescode/spline';
  import SimplexNoise from 'simplex-noise';

  export default {
    name: 'AppSvg',
    data() {
      return {
        width: null,
        height: null,
      }
    },
    mounted() {
      const path = document.querySelector('#circle');
      const root = document.querySelector('#svg');

      let hueNoiseOffset = 0;
      let noiseStep = 0.005;

      const simplex = new SimplexNoise();

      const points = createPoints();

      (function animate() {
        path.setAttribute("d", spline(points, 1, true));

        // for every point...
        for (let i = 0; i < points.length; i++) {
          const point = points[i];

          // return a pseudo random value between -1 / 1 based on this point's current x, y positions in "time"
          const nX = noise(point.noiseOffsetX, point.noiseOffsetX);
          const nY = noise(point.noiseOffsetY, point.noiseOffsetY);
          // map this noise value to a new value, somewhere between it's original location -20 and it's original location + 20
          const x = map(nX, -1, 1, point.originX - 20, point.originX + 20);
          const y = map(nY, -1, 1, point.originY - 20, point.originY + 20);

          // update the point's current coordinates
          point.x = x;
          point.y = y;

          // progress the point's x, y values through "time"
          point.noiseOffsetX += noiseStep;
          point.noiseOffsetY += noiseStep;
        }

        const hueNoise = noise(hueNoiseOffset, hueNoiseOffset);
        const hue = map(hueNoise, -1, 1, 0, 360);

        root.style.setProperty("--startColor", `hsl(${hue}, 82%, 96%)`);
        root.style.setProperty("--stopColor", `hsl(${hue + 60}, 82%, 96%)`);

        hueNoiseOffset += noiseStep / 6;

        requestAnimationFrame(animate);
      })();

      function map(n, start1, end1, start2, end2) {
        return ((n - start1) / (end1 - start1)) * (end2 - start2) + start2;
      }

      function noise(x, y) {
        return simplex.noise2D(x, y);
      }

      function createPoints() {
        const points = [];
        const numPoints = 6;
        const angleStep = (Math.PI * 2) / numPoints;
        const rad = 140;

        for (let i = 1; i <= numPoints; i++) {
          const theta = i * angleStep;

          const x = 250 + Math.cos(theta) * rad;
          const y = 50 + Math.sin(theta) * rad;

          points.push({
            x: x,
            y: y,

            originX: x,
            originY: y,

            noiseOffsetX: Math.random() * 1000,
            noiseOffsetY: Math.random() * 1000
          });
        }

        return points;
      }

      path.addEventListener("mouseover", () => {
        noiseStep = 0.01;
      });

      path.addEventListener("mouseleave", () => {
        noiseStep = 0.005;
      });
    }, 
    created() {
      this.resize();
      window.addEventListener("resize", this.resize);
    },
    unmounted() {
      window.removeEventListener("resize", this.resize);
    },
    methods: {
      setSize(ww){
        const size = ww * 0.28946;
        this.width = size;
        this.height = size * 0.57879;
      },
      resize(){
        const ww = document.body.clientWidth;
        this.setSize(ww);
      }
    },
  }
</script>

